import { Component } from 'react'; 
import PropTypes from 'prop-types';
import MarvelServise from '../../services/MarvelService';
import ErrorMessage from '../errorMessage/ErrorMessage';
import Spinner from '../spinner/Spinner';
import Skeleton from '.././skeleton/Skeleton'

import './charInfo.scss';

class CharInfo extends Component {
    state = {
        char: null,
        loading : false,
        error: false
    }

    marvelService = new MarvelServise();

    onCharLoaded = (char) => {
        this.setState({char, loading : false})
    }
    onError = () => {
        this.setState({
            loading: false,
            error: true
        })
    }

    oncharloading = () => {
        this.setState({ loading : true })
    }
    updateChar = () => {
        const {charId} = this.props
        if(!charId) {
            return;
        }
        this.oncharloading()
        this.marvelService
            .getCharacter(charId)
            .then(this.onCharLoaded)
            .catch(this.onError)
    }

    componentDidMount() {
        this.updateChar()
    }
    componentDidUpdate(prevProps, prevState) {
        if (this.props.charId !== prevProps.charId) {
            this.updateChar()
        }

    }


   render() {
    const {char, loading, error} = this.state
    const skeleton =  char || loading || error ? null : <Skeleton/>
    const spinner = loading ? <Spinner/> : null
    const errorMessage = error ? <ErrorMessage/> : null
    const context = !(loading || error || !char) ? <View char={char}/> : null
    return (
        <div className="char__info">
            {skeleton}
            {errorMessage}
            {spinner}
            {context}
        </div>
    )
   }
}

const View = ({char}) => {
    const {name, description, thumbnail, homepage, wiki, items} = char
    const plug = 'http://i.annihil.us/u/prod/marvel/i/mg/b/40/image_not_available.jpg'
    return (
        <>
            <div className="char__basics">
                <img src={thumbnail} alt={name} style={thumbnail === plug  ? {objectFit: 'contain'} : null}/>
                <div>
                    <div className="char__info-name">{name}</div>
                    <div className="char__btns">
                        <a href={homepage} className="button button__main">
                            <div className="inner">homepage</div>
                        </a>
                        <a href={wiki} className="button button__secondary">
                            <div className="inner">Wiki</div>
                        </a>
                    </div>
                </div>
            </div>
            <div className="char__descr">
                {description}
            </div>
            <div className="char__comics">Comics:</div>
                <ul className="char__comics-list">
                    {items.length === 0 ? 'no comics' : null}
                    {items.map((item, i) => {
                        return (
                                <li className="char__comics-item" key={i}>
                                    {item.name}

                                </li>
                        )
                    }).slice(0,10)}

                </ul>
        </>
    )
}
CharInfo.propTypes = {
    charId: PropTypes.number
}

export default CharInfo;