import { Component } from 'react';
import PropTypes from 'prop-types';
import ErrorMessage from '../errorMessage/ErrorMessage';
import MarvelServise from '../../services/MarvelService';
import Spinner from '../spinner/Spinner';

import './charList.scss';


class CharList extends Component {

    state = {
        data: [],
        loading : true,
        error: false,
        newItemLoading: false,
        offset: 210,
        charEnded: false
    }

    marvelServise = new MarvelServise()

    onCharLoaded = (newdata) => {
        let ended = false;
        if(newdata.length < 9) {
            ended = true
        }
        this.setState(({offset, data}) => ({
            data: [...data, ...newdata], 
            loading: false, 
            newItemLoading: false,
            offset: offset + 9,
            charEnded: ended
        }))
    }
    onError = () => {
        this.setState({
            loading: false,
            error: true
        })
    }


    componentDidMount() {
        this.setState({loading: true})
        this.onRequest();
    }

    onRequest = (offset) => {
        this.onCharListLoading()
        this.marvelServise
            .getAllCharacters(offset)
            .then(this.onCharLoaded)
            .catch(this.onError)
    }
    onCharListLoading() {
        this.setState({ newItemLoading: true })
    }

    render() {
        const plug = 'http://i.annihil.us/u/prod/marvel/i/mg/b/40/image_not_available.jpg'
        const {data, loading, error, newItemLoading, offset, charEnded} = this.state
        const spinner = loading ? <Spinner/> : null
        const errorMessage = error ? <ErrorMessage/> : null;
        const context = !(loading || error) ?
            data.map(item => (
                <li className="char__item" key={item.id} onClick={() => this.props.onCharSelected(item.id)}>
                    <img src={item.thumbnail} alt={item.name} style={item.thumbnail === plug  ? {objectFit: 'contain'} : null}/>
                    <div className="char__name">{item.name}</div>
                </li>
            ))
            : null
        return (
            <div className="char__list">
                    {spinner}
                <ul className="char__grid">
                    {context}
                    {errorMessage}
                </ul>
                <button 
                    className="button button__main button__long" 
                    disabled={newItemLoading} 
                    onClick={() => this.onRequest(offset)} 
                    style={{display: charEnded ? 'none': 'block'}}
                >
                    <div className="inner">load more</div>
                </button>
            </div>
        )
    }
    
}
CharList.propTypes = {
    onCharSelected: PropTypes.func
}
export default CharList;